<template lang="pug">
.statistics__graphics-content
    h4(v-if='index==1').statistics__graphics-title График учеников по дате
    h4(v-if='index==2').statistics__graphics-title График учеников по департаментам и дате
    .statistics__graphics-content  
        .statistics__limits Выберите временный диапазон:
        .statistics__input-content
            .statistics__input c:
            input(@change='changeDate()' type='date' v-model="startDate" :max='endDate').input__date
        .statistics__input-content
            .statistics__input до: 
            input(@change='changeDate()' type='date' v-model="endDate" :min='startDate' :max='new Date().toISOString().slice(0,10)').input__date
        scholars-line(
            v-if='index==1'
            :chartData='chartDataLineScholars'
        )
        departments-scholars-line(
            v-if='index==2'
            :chartData='chartDataDepartmentsScholars'
        )

</template>
<script>
import {mapState, mapActions} from "vuex";
export default {
    components: {
        scholarsLine: () => import("../../admin/statistics/scholars-line"),
        departmentsScholarsLine: () => import("../../admin/statistics/departments-scholars-line")
    },
    data() {
        return {
            endDate: new Date().toISOString().slice(0,10),
            startDate: ''
        }
    },
    props: {
        index: Number
    },
    computed: {
        ...mapState ("chartDataLineScholars", {
            chartDataLineScholars: state => state.chartDataLineScholars}),
        ...mapState ("chartDataDepartmentsScholars", {
            chartDataDepartmentsScholars: state => state.chartDataDepartmentsScholars})
    },
    created () {
        this.fillStartDate(),
        this.fetchData()
    },
    methods: {
        ...mapActions("chartDataLineScholars", ["fetchChartDataLineScholars"]),
        ...mapActions("chartDataDepartmentsScholars", ["fetchChartDataDepartmentsScholars"]),
        fetchData() {
            if(this.index==1) this.fetchChartDataLineScholars([this.startDate, this.endDate])
            else this.fetchChartDataDepartmentsScholars([this.startDate, this.endDate])
        },
        fillStartDate(){
            var today = new Date()
            today.setFullYear(today.getFullYear()-1)
            this.startDate=today.toISOString().slice(0,10)
        },
        changeDate() {
            if(this.index==1) this.fetchChartDataLineScholars([this.startDate, this.endDate])
            else this.fetchChartDataDepartmentsScholars([this.startDate, this.endDate])
        }
    }
}
</script>
