}
.statistics__limits {
    color: #ffe9ce;
    width: 100%;
    padding: 5% 0;
}
.statistics__input {
    width: 10%;
}
.statistics__input-content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: 5%;
    color: #ffe9ce;
}
.input__date{
    width: 200px;
